/colors.scss';\n\n.overview-obf {\n  position: relative;\n  border: 1px solid $--color-secondary;\n  border-radius: 6px;\n  padding: 0 4px;\n  height: auto;\n  margin-top: 6px;\n  margin: 0;\n\n  .overview-obf__label {\n    top: -10px;\n    left: 20px;\n    position: absolute;\n    color: $--color-secondary;\n    background-color: $--color-white;\n    padding: 0 3px;\n    font-size: 13px;\n    padding: 0;\n  }\n\n  .overview-obf__textarea {\n    border: none;\n    width: 100%;\n    height: auto;\n    line-height: 20px;\n    color: $--color-text-primary;\n    resize: none;\n    padding: 3px;\n    padding: 0;\n  }\n\n  .overview-obf__link {\n    color: $--color-text-secondary;\n    display: block;\n    transition: .2s ease-out color;\n    &:hover {\n      color: $--color-text-primary;\n    }\n  }\n\n  .overview-obf__textarea {\n    &--shadow {\n      bottom: 0;\n      top: 0;\n      left: 0;\n      right: 0;\n      position: absolute;\n      border-radius: 6px;\n      background-image: linear-gradient(to top, rgba(255, 255, 255, 85), rgba(255, 255, 255, 0) 60%);\n      transition: .2s ease-out;\n      opacity: 1;\n    }\n    &--shadow-hidden {\n      opacity: 0 !important;\n      visibility: hidden !important;\n    }\n  }\n\n  &:hover {\n    .overview-obf__textarea--shadow {\n      opacity: 0;\n      visibility: hidden;\n    }\n  }\n}\n\n@media (max-width: 900px) {\n  .overview-obf {\n    .overview-obf__textarea--shadow {\n      display: none;\n    }\n  }\n}\n","/* Colors\n-------------------------- */\n\n$--color-white: #fff !default;\n$--color-black: #424242 !default;\n$--color-gray: #979797 !default;\n$--color-gray-light-1: mix($--color-white, $--color-gray, 10%) !default;\n$--color-light-gray: #f3f4f5 !default;\n$--color-whatsapp: #075E54 !default;\n\n$--pj-color-darker-blue: #0A0022 !default;\n$--pj-color-dark-blue: #280BA0 !default;\n$--pj-color-blue: #0756e4 !default;\n$--pj-color-light-blue: #018EF0 !default;\n$--pj-color-lighter-blue: #30A9FB !default;\n$--pj